import PropTypes from 'prop-types'
import React, { Component } from 'react'
import logo from '../assets/fundoBranco.png'
import sintomaImage from '../assets/sintoma.svg'
import RecantoMaravilha from './RecantoMaravilha'
import logoHerms from '../assets/logo-ccb-light.png'
import Lugar from '../assets/armchair.svg'

import {
  Button,
  Form,
  Container,
  Divider,
  Grid,
  Header,
  Icon,
  Image,
  List,
  Menu,
  Responsive,
  Segment,
  Sidebar,
  Visibility,
  Label,
  Radio
} from 'semantic-ui-react'

var logado = true


const getWidth = () => {
  const isSSR = typeof window === 'undefined'
  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth
}



/* eslint-disable react/no-multi-comp */
/* Heads up! HomepageHeading uses inline styling, however it's not the best practice. Use CSS or styled components for
 * such things.
 */
const HomepageHeading = ({ mobile }) => (
  <Container text>
    <Image   src={logoHerms} large centered  style={{paddingTop: '3em' }}/> 
  </Container>
)

HomepageHeading.propTypes = {
  mobile: PropTypes.bool,
}

/* Heads up!
 * Neither Semantic UI nor Semantic UI React offer a responsive navbar, however, it can be implemented easily.
 * It can be more complicated, but you can create really flexible markup.
 */
class DesktopContainer extends Component {
  constructor (props){
    super(props)
    this.state = {
      login:false
    }
  }
 

  hideFixedMenu = () => this.setState({ fixed: false })
  showFixedMenu = () => this.setState({ fixed: true })

  callFormLogin = ()=> this.setState({login:true})

  render() {
    const { children } = this.props
    const { fixed } = this.state

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}
        >
          <Segment
            //inverted
            textAlign='center'
            style={{ minHeight: 700, padding: '1em 0em',backgroundImage: `url(${logo})` ,backgroundPosition: 'center', backgroundSize: 'cover',backgroundRepeat: 'no-repeat'}}
            vertical
          >
            <Menu
              fixed={fixed ? 'top' : null}
              //inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size='huge'
            
              style={{borderWidth:'0px' ,fontSize:'1.5em',fontWeight:'900',color:'#60B061'}}
            >
              <Container style={{borderWidth:'0px' ,fontSize:'1.em',fontWeight:'900',color:'#60B061'}}>
                <Menu.Item as='a' active style={{color:'#60B061'}}>
                  Reservador
                </Menu.Item>
                <Menu.Item as='a'>Lista de Confirmação</Menu.Item>
              </Container>
            </Menu>
            <HomepageHeading />
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    )
  }
}

DesktopContainer.propTypes = {
  children: PropTypes.node,
}

class MobileContainer extends Component {
  state = {}

  handleSidebarHide = () => this.setState({ sidebarOpened: false })

  handleToggle = () => this.setState({ sidebarOpened: true })

  render() {
    const { children } = this.props
    const { sidebarOpened } = this.state

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}
      >
        <Sidebar
          as={Menu}
          animation='push'
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}
        >

          <Menu.Item as='a'>Reservador</Menu.Item>
          <Menu.Item as='a'>Lista de Confirmação</Menu.Item>
{/*           <Menu.Item as='a'>Serviços</Menu.Item>
          <Menu.Item as='a'>Afiliados</Menu.Item>
          <Menu.Item as='a'>Loja</Menu.Item> */}
        </Sidebar>

        <Sidebar.Pusher dimmed={sidebarOpened}>
          <Segment
            inverted
            textAlign='center'
            /* style={{ minHeight: 350, padding: '1em 0em' }} */
            style={{ minHeight: 350, padding: '1em 0em',backgroundImage: `url(${logo})` ,backgroundPosition: 'center', backgroundSize: 'cover',backgroundRepeat: 'no-repeat'}}
            vertical
          >
            <Container>
              <Menu  
                pointing 
                secondary 
                size='large'
                style={{borderWidth:'0px' ,fontSize:'1.5em',fontWeight:'900',color:'#60B061'}}>
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name='sidebar' />
                </Menu.Item>
              </Menu>
            </Container>
            <HomepageHeading mobile />
          </Segment>

          {children}
        </Sidebar.Pusher>
      </Responsive>
    )
  }
}

MobileContainer.propTypes = {
  children: PropTypes.node,
}

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer>{children}</DesktopContainer>
    <MobileContainer>{children}</MobileContainer>
  </div>
)

ResponsiveContainer.propTypes = {
  children: PropTypes.node,
}

