import React, { Component } from 'react'
import { Button, Header, Image, Modal,Label,Dropdown,Popup } from 'semantic-ui-react'
import Lugar from '../assets/armchair.svg'

const membros = [
    { key: '1', value: 'Silas Viana', text: 'Silas Viana' },
    { key: '2', value: 'Teneval Teodoro',  text: 'Teneval Teodoro' },
    { key: '3', value: 'Gildézio',  text: 'Gildézio' },
    { key: '4', value: 'Dalva',  text: 'Dalva' },
    { key: '4', value: 'Dalva',  text: 'Dalva' },
    { key: '4', value: 'Ivone',  text: 'Ivone' },
    { key: '4', value: 'Toninho',  text: 'Toninho' },
  ]

class Assento extends Component {
    constructor(props) {
        super(props)
        this.state={open:false,visitante:''}
    }

    handleOpen = ()=> this.setState({open:true})
    handleClose = ()=> this.setState({open:false})

    
    confirmaPosicao = ()=>{
        this.props.handleConfirma(this.props.fileira,this.props.banco,this.props.posicao.id,this.state.visitante)
        this.handleClose()
    }

    handleSelect = (e,{value})=>{
        this.setState({visitante:value})
    }
    

    render () {
        return (
            <React.Fragment>
            {this.props.posicao.status
            ?  <Popup
            content={this.props.posicao.visitante}
            on='click'
            pinned
            trigger={<Label as='a' color={this.props.posicao.status ? 'green':''} onClick={this.handleOpen}><Image avatar  src={Lugar} /></Label>}
          />
          :
            <Modal
            open={this.state.open}
            trigger={<Label as='a' color={this.props.posicao.status ? 'green':''} onClick={this.handleOpen}><Image avatar  src={Lugar} /></Label>}
          >
            <Modal.Header>Confirmação de Assento - FILA {this.props.fileira} - BANCO - {this.props.banco} - POSICAO - {this.props.posicao.id}</Modal.Header>
            <Modal.Content image>
              <Image size='medium' src={Lugar} wrapped />
              <Modal.Description>
                <Header>Selecione um visitante na lista</Header>
                <Dropdown
                    placeholder='Selecione o visitante'
                    fluid
                    search
                    selection
                    options={membros}
                    onChange = {this.handleSelect}
                    value = {this.state.visitante}
                />
              </Modal.Description>
            </Modal.Content>
            <Modal.Actions>
              <Button color='black' onClick={this.handleClose}>
                Cancelar
              </Button>
              <Button
                content="Confirmar presença"
                labelPosition='right'
                icon='checkmark'
                onClick = {this.confirmaPosicao}
                positive
                disabled = {this.state.visitante == ''}
              />
            </Modal.Actions>
          </Modal>}
          </React.Fragment>
        )
    }
}

export default Assento