import React, { Component } from 'react'
import {Grid,Segment,Image,Label} from 'semantic-ui-react'
import Assento from './Assento'


const lugaresIrmas = [
        {fileira:1,
            bancos:[
                    {banco:1,posicoes:[
                        {id:1,posicao:'esquerda',status:false, visitante:false},
                    ]},
                    {banco:2,posicoes:[
                        {id:1,posicao:'direita',status:false, visitante:false}
                    ]},
                    {banco:3,posicoes:[
                        {id:1,posicao:'esquerda',status:false, visitante:false}
                    ]},
                    {banco:4,posicoes:[
                        {id:1,posicao:'direita',status:false, visitante:false}
                    ]},
                    
                ]
        },
        {fileira:2,
            bancos:[
                {banco:1,posicoes:[
                    {id:1,posicao:'esquerda',status:false, visitante:false}
                ]},
                {banco:2,posicoes:[
                    {id:1,posicao:'direita',status:false, visitante:false}
                ]},
                {banco:3,posicoes:[
                    {id:1,posicao:'esquerda',status:false, visitante:false}
                ]},
                {banco:4,posicoes:[
                    {id:1,posicao:'direita',status:false, visitante:false}
                ]},
                {banco:5,posicoes:[
                    {id:1,posicao:'esquerda',status:false, visitante:false}
                ]},
                {banco:6,posicoes:[
                    {id:1,posicao:'direita',status:false, visitante:false}
                ]},
                {banco:7,posicoes:[
                    {id:1,posicao:'esquerda',status:false, visitante:false}
                ]},
                {banco:8,posicoes:[
                    {id:1,posicao:'direita',status:false, visitante:false}
                ]},
              
            ]
        },
    ]

class RecantoMaravilha extends Component {

    constructor(props){
        super(props)
        this.state={layout:[],planejamento:[]}
    }
    //}

    confirmaPosicao = (fileiraSelectd,bancoSelectd,posicaoSelectd,visitante)=>{
        console.log(fileiraSelectd,bancoSelectd,posicaoSelectd,visitante)
        let contexto = this
        let layoutNovo = [...this.state.planejamento]
        layoutNovo[fileiraSelectd-1].bancos[bancoSelectd-1].posicoes[posicaoSelectd-1].status = true
        layoutNovo[fileiraSelectd-1].bancos[bancoSelectd-1].posicoes[posicaoSelectd-1].visitante = visitante
        this.setState({planejamento:layoutNovo},()=>{
            contexto.buildLayout()
        })

    }

    componentDidMount = ()=>{
        let contexto = this
        this.setState({planejamento:lugaresIrmas},()=>{
            contexto.buildLayout()
        })
    }

    buildLayout = ()=>{
        let layout = this.state.planejamento.map((item,index)=>{
            return(<Grid.Column width={8}>
                {item.bancos.map(banco=>{
                    if(banco.block){
                    return <Segment secondary key={banco.banco} textAlign='center' style={{height:'90vh'}}>{banco.block}</Segment> 
                    }else{
                        return (
                            <Segment key={banco.banco} >
                                  <Grid>
                                    <Grid.Row columns={banco.posicoes.length}>
                                        {banco.posicoes.map(posicao=>{
                                            let alinhamento = posicao.posicao ==='esquerda' ? 'flex-start':'flex-end'
                                            return <Grid.Column style={{display:'flex',justifyContent: alinhamento}}><Assento handleConfirma={this.confirmaPosicao} fileira={item.fileira} banco={banco.banco} posicao={posicao}/></Grid.Column>
                                        })}
                                    
                                    </Grid.Row>
                                </Grid>
                            </Segment>                      
                        )
                    }

                })}
            </Grid.Column>)
        })
        this.setState({layout:layout})
    }

    render () {
        return (
            <Grid>
                <Grid.Row>
                <Grid.Column width={16}><Segment textAlign='center'>Entrada</Segment></Grid.Column>
                </Grid.Row>
                <Grid.Row>
                    {this.state.layout}
                </Grid.Row>
            </Grid>
        )
    }
}

export default RecantoMaravilha